
import React from 'react';
import styles from './ShowEducationResume.module.css'

const ShowEducationResume=(props)=>{

    const educations=[
        {date:"2018 - 2021",title:"undergraduate degree (licence)",label:{place:"Higher Institute of Computer Science (ISI Ariana)",description:"I'm currently studying Computer Science and Software Systems in this institute."}},
        {date:"2014 - 2018",title:"Bachelor's degree (informatics)",label:{place:"Dar Chaabane El Fehri high school",description:"I succeeded in the National Baccalaureate Competition with mention Good."}},
    ]
    return(
        <ul className={`${styles.List} list`}>
            {
                educations.map(education=>{
                    return(
                        <li key={education.title}>
                            <span></span>
                            <div className={`${styles.Media} media`}>
                                <div className={`${styles.dFlex} d-flex`}>
                                    <p>{education.date}</p>
                                </div>
                                <div className={`${styles.mediaBbody} media-body`}>
                                    <h4>{education.title}</h4>
                                    <p style={{marginBottom:"10px"}}><strong>{education.label.place} </strong></p>
                                    <p>{education.label.description}</p>
                                </div>
                            </div>
                        </li>)
                })
            }
		</ul>
    );
}

export default ShowEducationResume

