import React from 'react'
import styles from './Input.module.css'
const Input = (props) =>{
    let stylesArr=`${styles.formControl}`
    let validationError = null;
    if (props.invalid && props.shouldValidate && props.touched){ 
        stylesArr=`${styles.formControl} ${styles.Invalid}`
        validationError=<p className={styles.ValidationError}>{props.errorMsg}</p>;
    }
    let inputElement=null
    switch(props.elementType){
        case 'input':
            inputElement= <input 
                            className={stylesArr} 
                            {...props.elementConfig} 
                            value={props.value} 
                            name={props.name}
                            onChange={props.changed} />;
            break;
        case 'textarea':
            inputElement= <textarea 
                            className={stylesArr} 
                            {...props.elementConfig} 
                            value={props.value} 
                            name={props.name}
                            onChange={props.changed} />;
            break;
        case 'select':
            inputElement= (<select 
                            className={stylesArr} 
                            value={props.value} 
                            onChange={props.changed} >
                                {
                                    props.elementConfig.options.map(option=>{
                                        return <option key={option.value} value={option.value}>{option.displayValue}</option>
                                    })
                                }
                            </select>);
            break;
        default:
            inputElement= <input 
                            className={stylesArr} 
                            {...props.elementConfig} 
                            value={props.value} 
                            onChange={props.changed} />;

    }
    
    return(
        <React.Fragment>
            {inputElement}
            {validationError}
        </React.Fragment>
    );
}
export default Input