import React,{useState} from 'react'
import styles from'./Navbar.module.css'
import SideDrawer from './SideDrawer'
import DrawerToogle from './DrawerToogle'
import Logo from './Logo'
import NavigationItems from './NavigationItems'

const Navbar = (props) =>{
    const [showSideDrawer,setShowSideDrawer]=useState(false)
    
    const sideDrawerClosedHandler= () =>{
        setShowSideDrawer(false)
    }

    const sideDrawerToogleHandler = () =>{
        setShowSideDrawer(prevShowSideDrawer=>({
            showSideDrawer:!prevShowSideDrawer.showSideDrawer
        }))
    }

    return(
        <React.Fragment>
            <SideDrawer 
                open={showSideDrawer} 
                closed={sideDrawerClosedHandler}
            />

            <header className={styles.headerArea} id="Home">
                <div className={styles.mainMenu}>
                    <nav className={`${styles.Navbar} navbar navbar-expand-lg`}>
                        <div className={`${styles.Container} container`}>
                            <DrawerToogle showSideDrawer={showSideDrawer} clicked={sideDrawerToogleHandler}/>
                            <Logo />
                            <NavigationItems />
                        </div>
                    </nav>
                </div>
            </header>
        </React.Fragment> 
    );
};
export default Navbar;

