import React from 'react'
import styles from './Home.module.css'
import img from '../../assets/images/me.jpg'
import {Link} from 'react-scroll'
const Home = (props) =>{
    return(
        <section className={styles.homeBannerArea}>
           	<div className={`${styles.Container} container`}>
           		<div className={`${styles.bannerInner} d-flex align-items-center`}>
					<div className={styles.bannerContent}>
						<div className={`${styles.Media} media`}>
							<div className={`${styles.dFlex} d-flex`}>
								<img src={img} alt="" className={styles.img}/>
							</div>
							<div className={`${styles.mediaBody} media-body`}>
								<div className={styles.personalText}>
									<h6>Hello Everybody, i am</h6>
									<h3>ALA HAMADI</h3>
									<h4>Software &amp; Web Developer Student</h4>
									<ul className={`${styles.basicInfo} list`}>
										<li><span><i className="lnr lnr-calendar-full"></i> 11th July, 1999</span></li>
										<li><span><i className="lnr lnr-phone-handset"></i> (+216) 28 546 891</span></li>
										<li><span><i className="lnr lnr-envelope"></i> ala.hamadi.01@gmail.com</span></li>
										<li><span><i className="lnr lnr-home"></i> Nabeul, Tunisia</span></li>
									</ul>
                                    <div className={styles.moreBtn}>
                                        <Link to="Contact" smooth={true} duration={1000} className={styles.mainBtn} style={{marginRight:"10px"}}>Get in Touch</Link>
										<a href="https://drive.google.com/file/d/1iJua0Cv9XsRDwzDIxE6REs2hwIMPeqQb/view?usp=sharing" target="_blank" rel="noopener noreferrer" className={styles.mainBtn}>Check My CV</a>
        		                    </div>
								</div>
							</div> 
						</div>
					</div>
				</div>
            </div>
        </section>

    );
};
export default Home;

