
import React from 'react';
import styles from './ShowExperienceResume.module.css'

const ShowExperienceResume=(props)=>{

    const experiences=[
        {date:"December 2019",title:"Problem Solving Coach",label:"Participation in organizing committee for both the TOP and the WinterCamp which is hosted by Informatics Tunisian Association."},
        {date:"September 2019",title:"TCPC Finals",label:"Participation in ACM Tunisian Collegiate Programming Contest Finals (Team: Puzzles)."},
        {date:"March 2019",title:"ISI CPC Qualifications",label:"Participation in ACM Tunisian Collegiate Programming Contest Qualifications (Team: Puzzles)."},
        {date:"March 2017",title:"TOP Finals",label:"Participation in Tunisian Olympiad Programming Finals (Solo)."},
    ]
    return(
        <ul className={`${styles.List} list`}>
            {
                experiences.map(experience=>{
                    return(
                        <li key={experience.title}>
                            <span></span>
                            <div className={`${styles.Media} media`}>
                                <div className={`${styles.dFlex} d-flex`}>
                                    <p>{experience.date}</p>
                                </div>
                                <div className={`${styles.mediaBbody} media-body`}>
                                    <h4>{experience.title}</h4>
                                    <p>{experience.label}</p>
                                </div>
                            </div>
                        </li>)
                })
            }
		</ul>
    );
}

export default ShowExperienceResume

