import React from 'react';
import styles from './Logo.module.css'

const Logo=(props)=>{

    function refreshPage() {
        window.location=window.location.href;
    }

    return(
        <ul className={`${styles.Nav} nav nav1 ml-auto`}>
            <li className={`${styles.navItem} nav-item`}><span className={`${styles.navLink} nav-link`} onClick={refreshPage}>Ala Hamadi</span></li>
        </ul>
    );
}

export default Logo