import React from 'react';
import styles from './NavigationItems.module.css'
import {Link} from 'react-scroll'
const NavigationItems=(props)=>{

    const links=["Home","Resume","Projects","Certifications","Contact"]
    
    return(
        <div className={"collapse navbar-collapse offset"}>
            <ul className={`${styles.Nav} nav ml-auto`}>
                {links.map(link=>{
                    return <li key={link} className={`${styles.navItem} nav-item`}><Link to={link} smooth={true} duration={1000} className={`${styles.navLink} nav-link`}>{link}</Link></li> 
                })}
            </ul>
        </div> 
    );
}

export default NavigationItems