import React from 'react';
import styles from './ProjectsFilter.module.css'

const ProjectsFilter=(props)=>{

    return(
        <ul className={`${styles.galleryFilter} list`}>
            <li className={props.toogle==="all" ? styles.active: ""} onClick={()=>props.clicked("all")}><span>All</span></li>
            <li className={props.toogle==="ReactJs" ? styles.active: ""} onClick={()=>props.clicked("ReactJs")}><span>ReactJs</span></li>
        </ul>
    );
}

export default ProjectsFilter