import React from 'react';
import styles from './Projects.module.css'
import SocialWheezy from '../../assets/images/SocialWheezy.PNG'
import Covid19 from '../../assets/images/Covid-19.PNG'
import Messenger from '../../assets/images/Messenger.png'
import Netflix from '../../assets/images/Netflix.PNG'

import M from 'materialize-css';
const AllProjects=(props)=>{
    document.addEventListener('DOMContentLoaded', function() {
        var elems = document.querySelectorAll('.tooltipped');
        M.Tooltip.init(elems,({enterDelay:400}));
    });
    const Projects=[
        {title:"Wheezy App",description:"Social media app where users can share, like, comment, update and much more.",image:SocialWheezy,
        technologies:[
                {languge:"ReactJs",description:"frontend"},
                {languge:"Redux",description:""},
                {languge:"React Hooks",description:""},
                {languge:"Firebase",description:"Backend,Database and authentication management"},
                {languge:"Material-UI",description:"frontend"},
                {languge:"Much More...✌",description:""},      
        ],
        github:"https://github.com/ala-hamadi/Social-Media-App",
        deployement:"https://react-social-media-app-b648b.web.app/"
        },
        {title:"Covid-19 Tracker App",description:"Tracking the impact of COVID-19 in the world.",image:Covid19,
        technologies:[
                {languge:"ReactJs",description:"frontend"},
                {languge:"React Hooks",description:""},
                {languge:"Material-UI",description:"frontend"},
                {languge:"Chart js",description:"charting"},
                {languge:"Covid-19 API",description:"integration"},
                {languge:"Much More...✌",description:""},      
        ],
        github:"https://github.com/ala-hamadi/Covid-19-Tracker/tree/main",
        deployement:"https://ala-hamadi.github.io/Covid-19-Tracker/"
        },
        {title:"Messenger Clone App",description:"A clone of Facebook Messenger. Built with Reactjs + Firebase.",image:Messenger,
        technologies:[
                {languge:"ReactJs",description:"frontend"},
                {languge:"React Hooks",description:""},
                {languge:"Material-UI",description:"frontend"},
                {languge:"Firebase",description:"Database"},
                {languge:"React-flip-move",description:"Animating a list of items"},
                {languge:"Much More...✌",description:""},      
        ],
        github:"https://github.com/ala-hamadi/Messenger-Clone",
        deployement:"https://react-facebook-messenger-app.web.app/"
        },
        {title:"Netflix Clone App",description:"A Netflix Clone made with React, Hooks and Firebase.",image:Netflix,
        technologies:[
                {languge:"ReactJs",description:"frontend"},
                {languge:"React Hooks",description:""},
                {languge:"Firebase",description:"Backend,Database and authentication management"},
                {languge:"Styled Components",description:"CSS"},
                {languge:"Fuse js",description:"Searching bar"},
                {languge:"Much More...✌",description:""},      
        ],
        github:"https://github.com/ala-hamadi/Netflix-Clone",
        deployement:"https://react-netflix-clone-app.web.app/"
        },
      ]

    return(
        <div className="row">
            {   
                Projects.map(Project=>{
                    return(
                    <div key={Project.title} className="col-lg-4 col-md-6 col-sm-6">
                        <div className={`${styles.Card} card medium`}>
                            <div className={`${styles.cardImage} card-image waves-effect waves-block waves-light`}>
                                <img
                                    alt="Screenshot of Interpol8r web app"
                                    src={Project.image}
                                    className="activator"
                                />
                            </div>
                            <div className={`${styles.cardContent} card-content`}>
                                <span className={`${styles.cardTitle} ${styles.tealText} card-title activator teal-text hoverline`}>
                                    {Project.title}<i className={`fa fa-ellipsis-v right ${styles.Right}`}></i>
                                </span>
                                <p>
                                    {Project.description}
                                </p>
                            </div>
                            <div className={`${styles.cardReveal} card-reveal`}>
                                <span className={`${styles.cardTitle} card-title`}
                                    >Accomplishments<i className="fa fa-times right"></i></span>
                                <ul>
                                    {
                                        Project.technologies.map(technologie=>{
                                            return <li><strong>{technologie.languge}</strong> {technologie.description}</li>
                                        })
                                    }
                                </ul>
                                <div className={`${styles.cardAction} card-action`}>
                                    <a
                                    aria-label="Visit Interpol8r web app"
                                    href={Project.deployement}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    data-position="top"
                                    data-tooltip="View Online"
                                    className={`${styles.btnFloating} btn-floating btn-large waves-effect waves-light tooltipped`}
                                    ><i className={`${styles.faExternalLink} fa fa-external-link`}></i></a>
                                    <a aria-label="Visit the GitHub repo for Interpol8r project"
                                    href={Project.github}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    data-position="top"
                                    data-tooltip="View Source"
                                    className={`${styles.btnFloating} btn-floating btn-large waves-effect waves-light brown tooltipped`}
                                    ><i className="fa fa-github"></i
                                    ></a>
                                </div>
                            </div>
                        </div>
                    </div>
                    )
                })
            }
        </div>  
    );
}

export default AllProjects