import React from 'react'
import styles from'./Footer.module.css'

import "../../fontawesome/font-awesome.min.css"

const Footer = (props) =>{
    return(
        <footer className={styles.footerArea}>
            <div className="container">
                <div className="row">
                    <div className={`${styles.socialWidget} col-lg-12`}>
                            <div className={styles.fTitle}>
                                <h3>Follow Me</h3>
                            </div>
                            <p>Let us be social</p>
                            <ul className={`${styles.List} list`}>
                                <li><a href="https://www.linkedin.com/in/ala-hamadi/" target="_blank" rel="noopener noreferrer"><i className="fa fa-linkedin"></i></a></li>
                                <li><a href="https://github.com/ala-hamadi" target="_blank" rel="noopener noreferrer"><i className="fa fa-github"></i></a></li>
                                <li><a href="https://codeforces.com/profile/KaisSaied" target="_blank" rel="noopener noreferrer"><span className="iconify" data-icon="simple-icons:codeforces" data-inline="false"></span></a></li>
                                <li><a href="https://www.facebook.com/ala.hamadi.503/" target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook"></i></a></li>         
                            </ul>
                            <p>Copyright &copy; 2020 All rights reserved | Made by Ala Hamadi</p>
                    </div>
                </div>
            </div>
        </footer>
    );
};
export default Footer;

