import React,{useState} from 'react'
import styles from './Projects.module.css'
import MainTitle from '../../UI/MainTitle/MainTitle'
import Between from '../../UI/Between/Between'
import AllProjects from './AllProjects'
import ReactJsProjects from './ReactJsProjects'
import ProjectsFilter from './ProjectsFilter'
import M from 'materialize-css';
import WOW from 'wowjs';
import "../../fontawesome/font-awesome.min.css"

const Projects = (props) =>{

    document.addEventListener('DOMContentLoaded', function() {
        var elems = document.querySelectorAll('.tooltipped');
        M.Tooltip.init(elems,({enterDelay:400}));
    });
    const [toogle,setToogle]=useState("all")

    new WOW.WOW({live: false}).init()



    const handlerToogle=(project)=>{
        setToogle(project)
    }

    let aff=<AllProjects />
        if (toogle==="ReactJs")
            aff=<ReactJsProjects />

    return(
        <React.Fragment>
            <Between />
            <section className={styles.homeGalleryArea} id="Projects">
                <MainTitle>My Projects</MainTitle>
                <div className={styles.isotopeFillter}>
        			<ProjectsFilter toogle={toogle} clicked={handlerToogle}/>
        		</div>
                <div className={`${styles.container} container`}>
                    <div className="wow fadeIn" data-wow-duration="3s" >
                        {aff}
                    </div>     
                </div>
            </section>
        </React.Fragment>
    );
};
export default Projects;

