import React,{useState} from 'react'
import styles from './Resume.module.css'
import ShowEducationResume from './ShowEducationResume'
import ShowExperienceResume from './ShowExperienceResume'
import MainTitle from '../../UI/MainTitle/MainTitle'
import WOW from 'wowjs';

const Resume = (props) =>{
	new WOW.WOW({live: false}).init()

	const [toogle,setToogle]=useState(true)
	const resumeTitle=toogle? "My Education" :"My Experiences"
	let carrer=<ShowEducationResume />
	if (toogle===false)
		carrer=<ShowExperienceResume />

    return(
		<section className={styles.myTabsArea} id="Resume">
			<div className={`${styles.Container} container`}>
				<MainTitle>Resume</MainTitle>
				<div>
					<ul className={`${styles.Nav} nav nav-tabs`}>
						<li className="nav-item">
							<span className={toogle ? "" : styles.active} onClick={()=>setToogle(true)}>My Education</span>
						</li>
						<li className="nav-item">
							<span className={!toogle ? "" : styles.active} onClick={()=>setToogle(false)}>My Experiences</span>
						</li>
					</ul>
					<div className={styles.resumeTitle}>
						<div className="wow fadeIn" data-wow-duration="2s" >
							<h3>{resumeTitle}</h3>
						</div>
					</div>
					<div className="tab-content" >
						<div className="wow fadeIn" data-wow-duration="2s" >
							{carrer}	
						</div>

					</div>
				</div>
			</div>
		</section>
    );
};
export default Resume;


